import test from 'ava';
import electronBuiltins from '.';

test('return an array of modules', t => {
	const result = electronBuiltins();
	t.pass(Array.isArray(result));
});

test('define new electron facade', t => {
	const result = electronBuiltins();
	t.pass(result.indexOf('electron') !== -1);
});

test('define deprecated exported modules', t => {
	const result = electronBuiltins();
	t.pass(result.indexOf('app') !== -1);
});

test('bring your own node builtins', t => {
	const result = electronBuiltins();
	t.pass(result.indexOf('fs') === -1);
});
